import styled from 'styled-components';
import { motion } from 'framer-motion';

export default function SlideFillMovie( props ){
    return <motion.div 
        className="slideFillMovie"
        initial={{ opacity: 0 }}
        whileInView={{ 
            opacity: 1,
            transition:{ delay: 0.2, duration: 0.8 },
        }}
        exit={{ opacity: 0 }}
        viewport={{ once: false, margin: "50px 0px 50px 0px" }}        
        >
        <div className="slideFillMovie__container">
            <div className="slideFillMovie__container__video">
                <ReactPlayer
                    url={props.video.default}
                    playing={true}
                    loop={true}
                    muted={true}
                    width="100%"
                    height="100%"
                    style={{ position: "absolute", top: 0, left: 0 }}
                />
            </div>
        </div>          
    </motion.div>
}


export const ProjectScreen = ( props ) => {
   
    const videoVariants = {
        initial:{ opacity: 0 },
        whileInView: { 
            opacity: 1,
            transition:{ delay: 0.2, duration: 0.8 },
        },
    }
    const getAnimations = ( anim ) => {
        
        if (anim === "" ) {
            return (
                {
                    initial:{ opacity: 0.5, scale: 1.1, y: 50 },
                    whileInView:{ 
                        opacity: 1, 
                        scale: 1, 
                        y: 0,
                        transition:{ 
                            delay: 0.2, 
                            duration: 0.8,
                        },
                    },
                    viewport:{ once: false, margin: "50px 0px 50px 0px" }
                }
            )
        }
        if (anim === "twovideos" ) {
            return (
                {
                    initial:{ opacity: 0, y: 50 },                   
                    whileInView:{ 
                        opacity: 1,      
                        y: 0,                   
                        transition:{
                            staggerChildren: 0.3,
                            delayChildren: 0.5, 
                            y: {
                                delay: 0,
                                duration: 0,
                            },
                            opacity: {
                                delay: 1,
                                duration: 1,
                            }
                         }
                    },      
                    viewport:{ once: false, margin: "0px 0px 0px 0px" }  
                }
            )
        }
    }
    
    return <StyledProjectScreen                 
                className={props.style}
                type={ props.layout } 
                initial={ getAnimations(props.animation).initial }
                whileInView={ getAnimations(props.animation).whileInView }
                exit={ getAnimations(props.animation).exit }
                viewport={ getAnimations(props.animation).viewport }                 
                video2origin="youtube"                
                >
               
                <ResponsiveImage
                    className="component-layout--main-image"
                    srcset={[ props.image ]}
                    alt={props.alt}
                    style={{ position: "absolute", width: "100%", height: "100%", objectFit: "cover"}}
                />

                { props.showVideo && <Video 
                    className="component-layout--video"
                    variants={videoVariants}
                    >
                    <ReactPlayer
                        url={props.video.default}
                        playing={true}
                        loop={true}
                        muted={true}                        
                        playsinline={true}
                        controls={false}
                        width="100%"
                        height="100%"
                        className="react-player"                        
                    />                   
                </Video> }

                { props.showVideo2 && <Video 
                    className="component-layout--video-2"
                    >
                    <ReactPlayer
                        //url={props.video2.default}
                        url={"https://www.youtube.com/watch?v=QltFFygh4UQ"}                        
                        playing={true}
                        loop={true}
                        muted={true}                        
                        playsinline={true}
                        controls={false}
                        className="react-player"     
                        width="100%"
                        height="100%"   
                        config={{
                            youtube: {
                                playerVars: { 
                                    showinfo: 0,
                                    modestbranding: 1,
                                    rel: 0,
                                    fs: 0,
                                    iv_load_policy: 3,
                                    cc_load_policy: 0,
                                    autohide: 1,
                                    disablekb: 1,
                                    playsinline: 1,
                                    controls: 0,
                                }
                            }
                        }}               
                    />                   
                </Video> }

                <motion.div className="content"
                    variants={container}
                    initial="hidden"
                    whileInView="show"                    
                    viewport={{ once: false, margin: "30px 0px -30px 0px" }}
                >
                    <StyledH1 layout 
                        initial={StyledH1Variants.hidden}
                        whileInView={StyledH1Variants.show}
                        exit={StyledH1Variants.hidden}
                        transition={StyledH1Variants.transition}

                        >{props.title}
                    </StyledH1>
                    <motion.div className="info" variants={ item }>
                        <motion.span className="label-creator">Criado por:</motion.span>
                        <motion.span className="creator">{props.creator}</motion.span>
                    </motion.div>
                    <motion.div className="info" variants={ item }>
                        <motion.span className="label-director">Dirigido por</motion.span>
                        <motion.span className="director">{props.director}</motion.span>
                    </motion.div>
                    <motion.div className="info" variants={ item }>
                        <motion.span className="label-writer">Escrito por</motion.span>
                        <motion.span className="writer">{props.writer}</motion.span>
                    </motion.div>
                    <motion.div className="info" variants={ item }>
                        <Link to={props.link}>
                            <button>Veja o projeto</button>
                        </Link>
                    </motion.div>
                </motion.div>

            </StyledProjectScreen>        
}

//default props
ProjectScreen.defaultProps = {
    layout: "default",
}

const Video = styled(motion.div)`
    position: relative;    
    top: 0;
    left: 0;    
    width: 100%;
    height: 100vh;    
    pointer-events: none;
    
    .react-player {
        position: relative;
        top: 0;
        left: 0;        
    }    
`

const StyledH1 = styled(motion.h1)`
    position: relative;  
    overflow: hidden;
    height: 3rem;
    width: var(--title-size, 0%);
    white-space: nowrap;

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: var(--undersize, 0%);
        height: 1px;
        background: #fff;
        
    }
`

const StyledH1Variants = {
    hidden: {
        "--undersize": "0%",
        "--title-size": "0%",
    },
    show: {
        "--undersize": "100%",
        "--title-size": "100%",
    },
    transition: {
        "--title-size": {
            delay: 0,
            duration: 1,
        },
        "--undersize": {
            delay: 0.5,
            duration: 0.5,
        }       
    }
}

