const variants = {
    default: {
        initial: {
            opacity: 0,
            x: -500
        },
        animate: {
            opacity: 1,
            x: 0
        },
        transition: {
            opacity: {
                delay: -2,
            }
        }
    },
    notext: {
        initial: {
            opacity: 0.99,            
        },
        animate: {
            opacity: 1,
        },
        transition: {
            opacity: {
                delay: 2,
                duration: 2
            },
        }
    },
}

 
const container = {
        hidden: { opacity: 0 },
        show: {
            opacity: 1,
            transition: {
                delayChildren: 1,
                staggerChildren: 0.15,
                staggerDirection: 1,
            }
        }
    }

    const item = {
        hidden: { opacity: 0 },
        show: { opacity: 1 },
        transition: {
            duration: 0.2,
        }
    }
